import {
    GRAPH_CHART,
    GRAPH_PIE,
    PROFILE_INFO,
    PROFILE_SET, PROJECT_INFO_GET,
    REGISTER_DELETE,
    REGISTER_FETCH,
    REGISTER_FETCH_TMP,
    REGISTER_SAVE,
    RSTUDIO_ERROR,
    RSTUDIO_EXECUTE,
    RSTUDIO_OK,
    SESSION_GET_ALL,
    STATS_KAPPA,
    STATS_KAPPA_PRO,
    STATS_KRIPPENDORF,
    STATS_MATRIX,
    TOOL_FETCH,
    TOOL_FETCH_DESIGN,
    TOOL_SET,
    VIDEOS_FETCH
} from './types';
import {showSuccess} from "../../components/utility/ShowNotification";
import siteConfig from "../../config/site.config";

export const baseUrl = siteConfig.apiUrl;

export const fetchVideos = () => dispatch => {
    fetch(baseUrl + '/videoData/getAll', {
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
        },
    })
        .then(res => res.json())
        .then(videos => {
            //Esto es incorrecto:   this.state.data = criteria.data;        //GET
            //Se debe realizar así: this.setState({data:criteria.data});    //SET
            //console.log(videos);
            dispatch({
                type: VIDEOS_FETCH,
                payload: videos
            })
        });
};

export const fetchRegister = () => dispatch => {
    //const register = sessionStorage.getItem(siteConfig.sessionStorageContainers.currentRegister);
    //console.log(`Valor de localstorage:${register}`);
    fetch(`${baseUrl}/register/get/`, {
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
        },
    })
        .then(res => {
            //console.log(res);
            return res.json();
        })
        .then(register => {
            //console.log(register);
            dispatch({
                type: REGISTER_FETCH,
                payload: register
            })
        });
};

export const fetchRegisterById = (uuid) => dispatch => {
    fetch(`${baseUrl}/register/get/${uuid}`, {
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
        },
    })
        .then(res => res.json())
        .then(register => {
            /*            console.log("fetchRegisterById");
                        console.log(register);*/
            dispatch({
                type: REGISTER_FETCH_TMP,
                payload: register
            })
        });
};

export const setProfileRegister = (registerId) => dispatch => {
    fetch(baseUrl + '/session/set/register/' + registerId, {
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
        },
    })
        .then(res => {
            //.then(res=>console.log(res.headers.get('set-cookie'));)
            return res.json();
        })
        .then(parameters => {
            //hacemos un set del registro seleccionado en localstorage
            //sessionStorage.setItem(siteConfig.sessionStorageContainers.currentRegister, registerId);
            showSuccess("Cambio de usuario", `Registro de observación activado para ${parameters.REGISTER}`);
            //console.log(parameters.REGISTER);
            dispatch(fetchRegister());
            dispatch(fetchSessionParameters());
        });
};

export const fetchProfileInfo = () => dispatch => {
    fetch(baseUrl + '/profile/get', {
        headers: {
            'Content-Type': 'application/json',
        },
    })
        .then(res => res.json())
        .then(parameters => {
            dispatch({
                type: PROFILE_INFO,
                payload: parameters.data[0]
            })
        });
};
export const saveProfileInfo = projectWrapper => dispatch => {
    fetch(baseUrl + '/profile/save', {
        method: 'POST',
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(projectWrapper)
    })
        .then(res => res.json())
        .then(parameters => {
            showSuccess("Actualización del proyecto", "Se ha guardado correctamente tu proyecto");
            fetchSessionParameters();
            dispatch({
                type: PROFILE_SET,
                payload: parameters.data[0]
            })
        });
}

