export const deleteRegister = sceneWrapper => dispatch => {
    fetch(baseUrl + '/register/clear', {
        method: 'POST',
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(sceneWrapper)
    })
        .then(res => res.json())
        .then(sceneResults =>
            dispatch({
                type: REGISTER_DELETE,
                payload: sceneResults
            })
        );
};

export const saveRegister = sceneWrapper => dispatch => {
    fetch(baseUrl + '/register/setMomentData', {
        method: 'POST',
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(sceneWrapper)
    })
        .then(res => res.json())
        .then(sceneResults => {
                fetchRegister();
                dispatch({
                    type: REGISTER_SAVE,
                    payload: sceneResults
                })
            }
        );
};

