import {notification} from "antd";

export default function(title, message) {
    notification.info({
        message: title,
        description: message,
        placement:"topRight"
    });
};

export function showError(title, message) {
    notification.error({
        message: title,
        description: message,
        placement:"topRight"
    });
}

export function showSuccess(title, message)  {
    notification.success({
        message: title,
        description: message,
        placement:"topRight"
    });
}